.case-studies-template-default .op-0 {
  opacity: 1;
}
.case-studies-template-default .op-0 .bg-image {
  opacity: 1;
}

.sk-blog .sk-block,
.sk-blog .sk-block-0 {
  padding-bottom: 30px;
  padding-top: 0px;
}
.sk-blog .sk-block .container,
.sk-blog .sk-block-0 .container {
  padding-left: 0px;
  padding-right: 0px;
}

.blog-filter .filter-checkboxes.row {
  justify-content: space-around;
}

@media screen and (min-width: 1025px) {
  .block--j .icon-container {
    display: flex;
    justify-content: stretch;
    align-items: stretch;
  }
}
@media screen and (max-width: 1024px) {
  .block--j .icon-container .icons--item {
    margin-bottom: 10px;
  }
  .block--j .icon-container .icons--item .img--container {
    padding-bottom: 10px;
    padding-top: 5px;
    max-height: 200px;
    max-width: 200px;
    margin: auto;
  }
}
.block--j .icon-container .icons--item:last-child .img--container::before {
  display: none;
  content: unset;
}
.block--j .icon-container .icons--item .img--container {
  height: 100%;
  position: relative;
  margin-bottom: 0;
}
.block--j .icon-container .icons--item .img--container img {
  height: 100%;
  object-fit: contain;
}
.block--j .icon-container .icons--item .img--container::before {
  content: "";
  display: block;
  position: absolute;
  background-position: center center;
  background-size: 2px 70%;
  background-repeat: no-repeat;
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/separator.svg");
}
@media screen and (min-width: 1025px) {
  .block--j .icon-container .icons--item .img--container::before {
    right: 0;
    height: 100%;
    width: 1.2px;
  }
}
@media screen and (max-width: 1024px) {
  .block--j .icon-container .icons--item .img--container::before {
    background-color: #939598;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1.2px;
  }
}

.sk-case-study .article-body > .article-content {
  margin-bottom: 100px;
}
.sk-case-study .article-body > .article-content .featured-img-holder {
  min-height: 450px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}
@media screen and (max-width: 1024px) {
  .sk-case-study .article-body > .article-content .featured-img-holder {
    min-height: 650px;
  }
}
.sk-case-study .article-body > .article-content .featured-img-holder::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(10.03deg, #002ea6 21.62%, rgba(0, 46, 166, 0) 84.26%);
}
.sk-case-study .article-body > .article-content .featured-img-overlay {
  position: relative;
  bottom: 0;
  left: 0;
  padding: 100px 10px 30px;
}
@media screen and (min-width: 500px) {
  .sk-case-study .article-body > .article-content .featured-img-overlay {
    padding: 45px 30px;
    position: absolute;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
}
.sk-case-study .article-body > .article-content .featured-img-overlay p,
.sk-case-study .article-body > .article-content .featured-img-overlay h1 {
  color: #fff;
  font-family: "Red Hat Display", sans-serif;
}
.sk-case-study .article-body > .article-content .featured-img-overlay h1 {
  margin-bottom: 0;
  font-size: 48px;
  font-weight: 700;
  line-height: 61px;
  word-break: break-word;
  hyphens: manual;
  -webkit-word-break: break-word;
  -webkit-hyphens: manual;
}
.sk-case-study .article-body > .article-content .featured-img-overlay .top-banner {
  display: flex;
  margin-bottom: 20px;
}
.sk-case-study .article-body > .article-content .featured-img-overlay .top-banner:before {
  content: "";
  display: block;
  background-image: url("/wp-content/themes/custom_theme/assets/media/svgs/case-study.svg");
  background-position: bottom right;
  background-repeat: no-repeat;
  background-size: contain;
  width: 47px;
  height: 47px;
  margin-right: 10px;
}
.sk-case-study .article-body > .article-content .featured-img-overlay .top-banner .post-type {
  width: fit-content;
  padding-bottom: 10px;
  font-size: 32px;
  font-weight: 700;
  line-height: 40px;
  text-align: left;
  border-bottom: 2px solid #fff;
}
.sk-case-study .article-aside .sk-sticky {
  margin-bottom: 2.5rem;
  margin-top: 1.5rem;
}
.sk-case-study .article-aside .sk-sticky svg {
  margin-top: 10px;
}

.blog-holder {
  padding-top: 0;
}